.article-title {
  @include font(500, 24px, 1.17, null);
  display: inline-block;
  margin-bottom: 30px;
  min-width: 288px;
  padding-top: 40px;
  width: 228px;
  @include tablet {
    @include font(600, 56px, 1, null);
    padding-top: 120px;
    width: 688px;
  }
  @include desktop {
    padding-top: 112px;
    width: 100%;
  }
}
.article-picture {
  @include tablet {
    margin-bottom: 0;
    border-radius: 12px;
  }
  @include desktop {
    min-width: 120px;
  }
}

.img-name {
  @include font(600, 16px, 1, null);
  margin-bottom: 8px;
  @include tablet {
    color: var(--accent);
    @include font(600, 24px, 1, null);
  }
}
.img-text {
  color: var(--secondary-text-color);
  @include font(600, 16px, 1, null);
  margin-bottom: 8px;
  @include tablet {
    @include font(600, 24px, 1, null);
  }
}
.img-wrap {
  margin-bottom: 20px;
  margin-left: -16px;
  margin-right: -16px;

  @include tablet {
    margin-left: 0;
    margin-right: 0;
    display: flex;
    gap: 40px;
    align-items: flex-end;
    margin-bottom: 16px;
  }
}
.img-text-wrap {
  @include tablet {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  @include desktop {
    flex-direction: row;
    gap: 96px;
    margin-bottom: 24px;
  }
}

.top-text {
  display: flex;
  flex-direction: column;
  gap: 20px;
  @include tablet {
    gap: 30px;
  }
  @include desktop {
    display: none;
  }
}
.top-text-desktop {
  @include desktop {
    display: block;
  }
}
.top-text-desktop .article-text:not(:last-child) {
  margin-bottom: 30px;
}
.middle-text {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: 'Roboto-400', sans-serif;
  @include font(400, 16px, 1.19, null);
  @include tablet {
    margin-top: 30px;
    margin-bottom: 30px;
    color: var(--accent);
    @include font(400, 24px, 1.17, null);
  }
}

.bottom-text {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-bottom: 48px;
  @include tablet {
    gap: 30px;
    padding-bottom: 120px;
  }
}
.text-wrap {
  padding-bottom: 48px;
  @include tablet {
    padding-bottom: 120px;
  }
}
.article-text {
  font-family: 'Roboto-400', sans-serif;
  @include font(400, 16px, 1.19, null);
  @include tablet {
    @include font(400, 24px, 1.17, null);
  }
}
.tablet-wrap {
  @include tablet {
    display: flex;
    gap: 40px;
    align-items: flex-end;
    margin-bottom: 16px;
  }
  @include desktop {
    display: none;
  }
}
.desktop-wrap {
  display: none;
  @include desktop {
    display: block;
    margin-bottom: 48px;
  }
}
.desktop-text-wrap {
  @include desktop {
    display: flex;
    flex-direction: column;
    width: 690px;
  }
}
