.mob-menu {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 104px;
    background-image: url(../images/background/frame-4521-m-1x.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transform: translateY(0);
    transition: transform var(--timing-function), opacity var(--timing-function), visibility var(--timing-function);

   @include retina {
        background-image: url(../images/background/frame-4521-m-2x.jpg);
   }
    
    @include tablet {
        top: 136px;
        background-image: url(../images/background/frame441-t-1x.jpg);

        @include retina {
            background-image: url(../images/background/frame441-t-2x.jpg);
        }
    }

    @include desktop {
        @media screen and (min-width: 1280px) {
        display: none;
        }
    }
}

.menu.is-hidden {
    transform: translateY(-100%);
}

.mob-menu__navigation-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    padding-top: 64px;
}

.mob-menu__navigation-link {
    display: block;
    text-transform: uppercase;
    color: var(--primary-text-color);

    @include font(700, 32px, 1.19, null);
}

.mob-menu-btn {
    padding: 0;
    border: none;
    fill: #86E543;
    cursor: pointer;
    background: transparent;

    @include desktop {
        display: none;
    }
}

.mob-menu-btn .icon-close-btn {
    display: none;
}

.mob-menu-btn.is-open .icon-open-btn {
    display: none;
}

.mob-menu-btn.is-open .icon-close-btn {
    display: block;
}