header {
    position: relative;
    z-index: 1;
    box-shadow: 0px 4px 10px rgba(97, 162, 58, 0.2), 0px 4px 50px rgba(94, 236, 54, 0.5), 0px 4px 4px rgba(17, 168, 26, 0.2);
}

.header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
    padding-bottom: 24px;

    @include tablet {
        padding-top: 40px;
        padding-bottom: 40px;
    }
}

.header-logo {
    max-width: 115px;
}

.header-navigation {
    @include media-max(1279px) {
        display: none;
    }
}

.header-navigation__list {
    display: flex;
    gap: 48px;
}

.header-navigation__link {
    display: block;
    color: var(--primary-text-color);
    transition: var(--timing-function);
    
    @include font(null, 16px, 1, null);
    
    &:hover,
    &:focus {
        color: var(--accent);
        text-shadow: var(--box-shadow);
    }
}

.btn-shop {
    @include media-max(767px) {
        display: none;
    }
}