.footer {
    background: #001414;
   background-position: center;
   margin: 0 auto;
   width: 100%;
   padding-top: 25px;
   padding-bottom: 24px;
   background-size: cover;
}

.footer-container {
    @media screen and (max-width: 479px) {
     width: 100%;
 }

 
  

 @include tablet {
    display: flex;
   align-items: flex-start;
   justify-content: space-between;
 }

 @include desktop {
    display: flex;
    justify-content: space-between;
 }

}

.footer__logo {
    font-family: var(--main-font);
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.17;
    text-align: center;
    color: var(--primary-text-color); 
    
}

.footer-nav__container {
    display: flex;
    justify-content: space-between;
    margin-top: 25px;

    @include tablet {
        margin-top: 0;
       margin-right: 73px;
       margin-left: 61px;
     }

     @include desktop {
        margin-top: 0;
        margin-right: 281px;
        margin-left: 118px;
     }

}



.footer-menue__item {
    
    margin-bottom: 16px;
}

.footer-menue__link {
    font-family: var(--main-font);
    // font-style: normal;
    font-weight: 400;
    font-size: 16px; 
    line-height: 1;
    color: var(--primary-text-color);
    
}

.social {
    
    margin-right: 16px;


    @include tablet {
        
        margin-right: 0;
     }

     @include desktop {
        display: block;
        margin-right: 0;
     }
}

.social__list {
    display: block;

    @include tablet {
        display: flex;
        margin-left: 89px;
     }

     @include desktop {
        display: block;
        margin-left: 217px;
     }
}

.social__item {
   margin-bottom: 32px; 

   @include tablet {
    margin-bottom: 0;
    margin-right: 16px;
 }  

 @include desktop {
    margin-bottom: 16px;
    margin-right: 0;
 }  
}

.social__item:last-child {
    margin-bottom: 0; 
    margin-right: 0;
    
 }

.social__icon {
    
     width: 24px;
     height: 24px;
    border-radius: 50%;
     background: #fff;
     display: flex;
     align-items: center;
    justify-content: center; 
    fill: #001414
    
}


.footer__text {
    font-family: var(--main-font);
    // font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.19;
    color: var(--primary-text-color);
    margin-bottom: 8px;
}

.footer-link {
    margin-bottom: 16px;
}

.footer-mail {
    font-family: var(--main-font);
    // font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.19;
    text-transform: capitalize;
    color: var(--secondary-text-color);
}

.footer-link__tel {
    margin-bottom: 8px;
}

.footer-tel {
    font-family: var(--main-font);
    // font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.19;
    letter-spacing: 0.01em;
    text-transform: capitalize;
    color: var(--secondary-text-color);
    margin-bottom: 8px;
}

